.Contact {
    width: 90%;
    margin: 1em 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
}

.Contact .profile {
    display: flex;
    justify-content: space-evenly;
    height: min-content;
    padding-top: 2em;
}

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    display: none;
    width: 40%;
    border-radius: 5%;
    margin: auto;
}

.profile-info {
    height: min-content;
    margin: auto;
}

.social {
    display: flex;
}